import React, { Component } from 'react';
import '../../yritys.css';
import validus from '../../images/validus.png';
import spiderlogo from '../../images/spider_logo.png';
import { getTranslate, getActiveLanguage, getLanguages, setActiveLanguage } from 'react-localize-redux';
import { connect } from 'react-redux';

class Yritys extends Component {


    render() {
        return (
            <div className="container yritys">
                <h2>YRITYSESITTELYT</h2>
                <hr />
               {/* <p>  I'm a title. Click here to add your own text and edit me.</p> */}
               <div className="company">
                
                            <div className="top-box">
                            <img src={validus} alt="validus" />
                            <p><a href="http://www.validus.fi/" target="_blank">Validus Solutions Oy</a> on Mad Boosterin tytäryhtiö ja sen tehtävänä on tuottaa nykyaikaisia selaimessa toimivia sovelluksia palveluna. Ensimmäiset tuotteet ovat jo pilottikäytössä ja ne keskittyvät henkilöstön hallintaan ja ohjaukseen. <br /> <br />

                                Palvelumalli ja lisätiedot Spiderin omilla www-sivuilla. <a href="http://www.validus.fi/" target="_blank">www.validus.fi</a>
                            </p>  
                            </div>

                            
                    </div>
             

               <div className="company">
                
                <div className="btm-box top-box ">
                <img src={spiderlogo} alt="spiderlogo" />
                <p><a href="http://www.spidersolutions.fi/" target="_blank">Spider Solutions Oy</a> liittyi Mad Boosterin osakkuusyhtiöksi lokakuussa 2017. Spiderin perustajat Joona Iso-Lotila ja Tomi Savolainen olivat kehittäneet terveydenhuoltoalalle toimintamallia, jolla asiakkaat säästävät aikaa ja tehostavat potilastyötä. Tässä kohtaa kumppaniksi valikoitui Mad Booster, joka toteutti järjestelmän toimintamallista tuotteeksi. 
                     <br /> <br />
                     Joona Iso-Lotila: "Yhteistyö on toiminut alusta saakka saumattomasti, tuotekehitys on ollut ketterää ja lopullinen tuote on jopa enemmän, kuin alkuun osasimme määritelläkkään. Aikataulun puolesta Mad Booster on pystynyt toteuttamaan alle kahdessa kuukaudessa ensimmäisen pilottiversion ja tarvittavat muutokset ovat myös syntyneet nopeammin kuin on ennalta arvioitu." 
                     <br /> <br />                  

                    Palvelumalli ja lisätiedot Spiderin omilla www-sivuilla. <a href="http://www.spidersolutions.fi/" target="_blank">www.spidersolutions.fi</a>
                </p>  

                
                </div>
        </div>
                
            </div>
        );
    }
}

const mapStateToProps = state => ({
    translate: getTranslate(state.locale),
    currentLanguage: getActiveLanguage(state.locale).code,
    languages: getLanguages(state.locale)
});
const mapDispatchToProps = { setActiveLanguage };

export default connect(mapStateToProps, mapDispatchToProps, null, {pure:false})(Yritys);



