import React, { Component } from 'react';
import '../../rekry.css';


class Rekry extends Component {


    render() {
        return (
            <div>
            <div className="rekry-head">
            <div className="container">
                <h1>AVOIMET TYÖPAIKAT</h1>
                <h2> 5.MAALISKUUTA 2018</h2>
            </div>
            </div>
            <div className=" container rekry-btm contact-head kaynti ">
            <h2>Rekrytoimme alkuvuonna 2018 useampaa kehittäjää </h2>
            <hr />
            <p>
                
            ja tässä myös onnistuimme mielestämme loistavasti. Saimme palkattua viisi uutta työkaveria itsellemme. Tällä hetkellä jokainen työpisteemme on täytetty, mutta jos omasta mielestäsi sopisit työporukkamme kylkeen luomaan uusia menestystarinoita, voit joka tapauksessa ottaa yhteyttä ja katsotaan jos vaikka lähitulevaisuudessa voitaisiin järjestää yhteistä tekemistä. <br /><br />

            Joku viisas mies aikoinaan sanoi, että aina pitää palkata itseään viisaampia ja nuorempia. Tällä opilla on hyvä mennä eteenpäin. <br /><br />



            Laita siis meille viestiä vapaamuotoisesti osoitteeseen <span className="green">rekry@madbooster</span>.com ja kerro meille omin sanoin miten haluaisit meidän rakentavan tulevaisuuden uraasi ja miten voisimme hyödyntää osaamistasi. 

            Jos kysymyksiä nousee pintaan, niin soita tai pistä viestiä Juhalle <span className="green">0400-626369</span>. <br /><br />
            Mad Booster on vuonna 2016 perustettu teknologiatalo, joka kehittää asiakkaillensa liiketoimintaa ja nykyaikaisia digitaalisia palveluita. <br /><br />

            Meidän työtämme on auttaa asiakkaitamme menestymään jatkuvasti muuttuvassa maailmassa.
            Uusien asioiden oppiminen, ymmärryksen kasvattaminen, vanhojen käytäntöjen kyseenalaistaminen ja parhaiden työtovereiden löytäminen ovat onnistumisemme ehdottomia edellytyksiä.<br /><br />

            Mad Boosterilla asiantuntijat pääsevät aina valitsemaan sopivimmat teknologiat ja menetelmät. Tällä tavoin varmistamme, että olemme edelläkävijöitä paitsi palveluiden laadussa, niin myös teknisessä arkkitehtuurissa ja toteutuksessa.<br /><br />

            Meillä työkaverisi ovat jo ohjelmistoalalla olleet pidempään, mutta älä turhaan anna sen estää itseäsi mikäli olet vasta urasi alussa.<br /><br />

            <span className="green">Tavoitteemme on palkata osaajat vakituisiin työsuhteisiin, kesätyö- ja harjoittelupaikkoja pelkästään emme pysty valitettavasti tarjoamaan.<br /><br />

            CV-pankkina käytämme</span><a href="https://www.tippoint.fi/fi/etusivu" target="_blank" rel="noopener noreferrer"> Tippoint</a><span className="green">-palvelua</span>
             </p>   
            </div>
            
            </div>

        );
    }
}

export default Rekry;



